/**
 *
 * CollectionCard
 *
 */

import React, { SFC } from 'react';
import { CollectionModel } from 'cb-utils/console-entity-models';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import NavCard from 'components/NavToDetailCard';
import { FormattedMessage } from 'react-intl';
import messages from './messages';

interface CollectionCardProps extends SystemRouteProps<{}> {
  collection: CollectionModel;
}

const CollectionDescription = ({
  collection,
}: {
  collection: CollectionModel;
}) => {
  if (!collection) return null;
  return (
    <div className="text-muted">
      {collection.isConnect ? (
        <React.Fragment>
          <FormattedMessage {...messages.connection} /> / {collection.dbtype}{' '}
          {collection.dbname} / {collection.tablename}{' '}
          <FormattedMessage {...messages.table} />
        </React.Fragment>
      ) : (
        ''
      )}
    </div>
  );
};

const CollectionCard: SFC<CollectionCardProps> = (props) => {
  const collectionName = props.collection.name;
  return (
    <NavCard name={collectionName}>
      <div data-cy="collection-link">{props.collection.name}</div>
      <CollectionDescription collection={props.collection} />
    </NavCard>
  );
};

export default withRouter(CollectionCard);



