/*
 *
 * CollectionsPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { collections } from 'containers/Console/ducks';
import CollectionCard from 'components/CollectionCard';
import CardTable from 'components/CardTableForEntities';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import { Button } from 'reactstrap';
import { AddIcon, DatabaseIcon, RolesIcon } from 'components/EntityIcons';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import CbDropDown from 'components/CbDropDown';
import { GearDropDown } from 'containers/EdgesPage';
import consoleMessages from 'containers/Console/messages';
import LearnMoreLink from 'components/LearnMoreLink';
import { createStructuredSelector } from 'reselect';

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.name} />
        </b>
      ),
      isSortable: true,
      sortKey: 'name',
    },
  ];
};

export class CollectionsPage extends React.PureComponent<
  IStateToProps & DispatchToProps,
  {}
> {
  componentWillMount() {
    this.props.fetchAll();
  }
  render() {
    const title = (
      <PageHeader
        header={
          <div>
            <DatabaseIcon /> <FormattedMessage {...messages.header} />
            <CbDropDown
              triggerEl={<GearDropDown data-cy="open-settings-dropdown" />}
              items={[
                {
                  displayName: (
                    <div>
                      <RolesIcon /> <FormattedMessage {...messages.editRoles} />
                    </div>
                  ),
                  'data-test': 'edit-overall-roles-btn',
                  onClick: () =>
                    this.props.createShowBaseModal({
                      component: 'EditRolesModal',
                      props: { assetClass: 'collections' },
                    }),
                },
              ]}
            />
          </div>
        }
        actionButton={this.renderAddButton}
      />
    );

    return (
      <ConsolePage
        subheader={title}
        isFetchingAssets={this.props.isFetchingCollections}
        zeroState={{
          show: this.props.totalCollections === 0,
          icon: <DatabaseIcon />,
          title: consoleMessages.assetClassCollections,
          info: messages.zeroStateInfo,
          moreInfo: (
            <LearnMoreLink url="https://docs.clearblade.com/v/3/1-platform_concepts/data/collection/" />
          ),
          addButton: this.renderAddButton({ size: 'lg' }),
        }}
      >
        <Helmet
          title="ClearBlade - Collections"
          meta={[
            { name: 'description', content: 'Description of CollectionsPage' },
          ]}
        />

        <CardTable
          exportCallback={() =>
            this.props.createShowBaseModal({
              component: 'ExportModal',
              props: { assetClass: 'collections' },
            })
          }
          defaultSortKey="name"
          headerColumns={createColumnsForCardTable()}
          entities={this.props.collections}
          entityCard={CollectionCard}
          cardProp="collection"
        />
      </ConsolePage>
    );
  }

  renderAddButton = ({ size = 'md' }) => (
    <Button
      size={size}
      onClick={() =>
        this.props.createShowBaseModal({
          component: 'NewCollectionModal',
          props: {},
        })
      }
      data-cy="add-collection"
      color="primary"
    >
      <AddIcon /> <FormattedMessage {...messages.collections} />
    </Button>
  );
}

type IStateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  collections: collections.selectors.wholeSlice,
  isFetchingCollections: collections.selectors.fetchAll.isLoading,
  totalCollections: collections.selectors.entitiesCount,
});

type DispatchToProps = typeof mapDispatchToProps;
const mapDispatchToProps = {
  fetchAll: collections.actions.fetchAll.request,
  createShowBaseModal,
};

export default connect(mapStateToProps, mapDispatchToProps)(CollectionsPage);



